import React, { Component } from "react";
import { connect } from "react-redux";
import { Cookies } from "react-cookie";
import { Button } from "react-bootstrap";
import { FontAwesomeIcon } from "../../Assets/AssetsHelper";
import Recaptcha from 'react-google-invisible-recaptcha';
import { userSignUp, getIPDetails } from '../../actions/loginActions'
import SocialSignIn from "./SocialAuthentication/SocialSignup";
import { sitekey, WEBSITE_URL } from "../../actions/api";
import { getCookieValue, containsSpecialChars, validateEmail, encourageEmail, validatePassword } from "../../utils";
import Form from 'react-bootstrap/Form';
import OtherAuthAction from "./Components/OtherAuthAction";
import { GET_GOOGLE_DATA, GOOGLE_AUTH_TOKEN } from "../../actions/socialUrl";
import { removeAuthLocalData } from "../../utils/addAccount";
import { Link } from "react-router-dom";
import PwdStrength from "../Layout/Main/CommonComponents/PwdStrength";
import GoogleLoginSuggestions from "./Components/GoogleLoginSuggestions";
import FormTerms from "./Components/FormTerms";

const cookies = new Cookies();
class SignUp extends Component {
    constructor(props) {
        super(props)
        this.recaptcha = React.createRef();
    }
    state = { accountFullName: null, afNError: null, accountEmailId: null, aEmailError: null, aEmailMessageError: null, accountPassword: null, reCaptcha: localStorage.getItem('_grecaptcha') ? localStorage.getItem('_grecaptcha') : null, loginLoader: false, aEmailEncourager: null, strength: 0, caseMatch: [], showPass: false, pwdError: false, strongPass: true }


    handleMessage = async (event) => {
        try {
            const response = JSON.parse(event.data);
            const { code, type } = response;
            if (event.origin.includes("tagshop.ai") || event.origin.includes("localhost")) {
                if (type == 'sign-login') {
                    if (code) {
                        const { data: { responseData } } = await GOOGLE_AUTH_TOKEN(code);
                        const { response, userInfo } = responseData || {}
                        removeAuthLocalData();
                        const obj = { accountFullName: userInfo?.name, accountEmailId: userInfo?.email, createFrom: 'google', picture: userInfo?.picture || "", authId: userInfo?.sub }
                        this.socialLogin(obj);
                    };
                }
            }
        } catch (error) {
        }
    };

    componentDidMount() {
        this.checkEmailFromUrl()
        window.addEventListener("message", this.handleMessage)

        const googleSignDetails = cookies.get('__googleAuthData');
        if (googleSignDetails && Object.keys(googleSignDetails).length > 0 && googleSignDetails.email && window.location.href.includes('?google-redirection')) {
            cookies.remove('__googleAuthData', { path: "/", domain: '.tagshop.ai' });
            this.onGSocialLogin("google", googleSignDetails);
        }
    }

    checkEmailFromUrl = () => {
        const url = window.location.href
        if (url.includes('email')) {
            const email = url.split('?')[1].split('&')[0].split('=')[1]
            if (email) { this.setState({ accountEmailId: decodeURIComponent(email) }) }
        }
    }

    readAuthentication = (event) => {
        const response = event.data ? JSON.parse(event.data) : '';
        this.socialLogin(response)
    }

    onFullNameChange = event => this.setState({ accountFullName: event.target.value, afNError: false })
    onEmailChange = event => this.setState({ accountEmailId: event.target.value, aEmailMessageError: null, aEmailError: false, aEmailEncourager: encourageEmail(event.target.value) })

    onClickToCreateAccount = event => {
        const { afNError, aEmailError, accountFullName, accountEmailId, accountPassword, reCaptcha, strength } = this.state;
        const { history } = this.props
        if (validateEmail(accountEmailId) && !afNError && !aEmailError && accountFullName && String(accountFullName).length >= 3 && !containsSpecialChars(accountFullName) && accountEmailId && String(accountEmailId).length > 0 && accountPassword && String(accountPassword).length >= 8 && reCaptcha && strength == 4) {
            getIPDetails().then((ipResponse) => {
                const { data } = ipResponse
                this.setState({ loginLoader: true })
                const trackingPage = getCookieValue('tracking_URL')
                const landingPage = getCookieValue('landing_URL')
                this.props.userSignUp({ fullName: accountFullName, emailId: accountEmailId, trackingPage: trackingPage, landingPage: landingPage, password: accountPassword, activeProduct: "2", createFrom: "signup", picture: "", oauth_uid: "", countryCode: data?.country ?? 'US' }, history, this.responseCallback)
            })
        }
        else {
            if (!accountFullName || String(accountFullName).length < 3 || containsSpecialChars(accountFullName)) this.setState({ afNError: true })
            if (!accountEmailId || String(accountEmailId).length == 0 || !validateEmail(accountEmailId)) this.setState({ aEmailError: true, aEmailEncourager: false })
            if (!accountPassword || String(accountPassword).length < 8) this.setState({ pwdError: true })
            if(strength != 4 ) this.setState({ strongPass: false })
            else {
                this.recaptcha.execute()
            }
        }
    }
    onResolved = () => {
        const recaptchaResolved = this.recaptcha.getResponse()
        this.setState({ reCaptcha: recaptchaResolved }, () => {
            localStorage.setItem('_grecaptcha', recaptchaResolved)
            this.onClickToCreateAccount()
        })
    }

    checkPassStrength = event => {
        const password = event.target.value;
        const { strength, caseMatch } = validatePassword(password);
        this.setState({ accountPassword: password, strength: strength, caseMatch, pwdError: false, strongPass: strength == 4 ? true : this.state.strongPass });
    }

    responseCallback = (data) => {
        if (data && Object.keys(data).length > 0) {
            this.recaptcha.reset();
            this.setState({ aEmailError: true, loginLoader: false, aEmailMessageError: data.response_message, aEmailEncourager: false })
        }
    }
    onGSocialLogin = (provider, data) => {
        if (data && Object.keys(data).length > 0) {
            getIPDetails().then((ipResponse) => {
                const { IpData } = ipResponse
                const trackingPage = getCookieValue('tracking_URL')
                const landingPage = getCookieValue('landing_URL')
                var picture = data?.picture || "";
                this.props.userSignUp({ fullName: data.name, emailId: data.email, password: "", trackingPage: trackingPage, landingPage: landingPage, activeProduct: '0', picture: picture, oauth_uid: data.sub, countryCode: IpData?.country || 'US', utm_source: provider }, this.props.history, this.responseCallback)
            })
        }
    }
    socialLogin = (socialData) => {
        var { accountFullName, accountEmailId, createFrom, picture, authId } = socialData
        var self = this;
        localStorage.setItem("socialLogin", 1)
        getIPDetails().then((ipResponse) => {
            const { data } = ipResponse
            const trackingPage = getCookieValue('tracking_URL')
            const landingPage = getCookieValue('landing_URL')
            this.props.userSignUp({ fullName: accountFullName, emailId: accountEmailId, password: "", trackingPage: trackingPage, landingPage: landingPage, activeProduct: '0', createFrom: createFrom, picture: picture, oauth_uid: authId, countryCode: data?.country || 'US', utm_source: createFrom == 'facebook' ? "facebook_ads" : createFrom }, self.props.history, () => {
            })
        })
    }

    componentWillUnmount() {
        window.removeEventListener("message", this.handleMessage);
    }
    onFormSubmit = e => {
        e.preventDefault();
    }

    render() {
        const { prefix } = this.props
        const { reCaptcha, afNError, aEmailError, accountFullName, accountEmailId, aEmailMessageError, loginLoader, showPass, aEmailEncourager, strength, pwdError, strongPass } = this.state
        return <div className="w-100 mb-lg-0 mb-3">
            {!window.location.href.includes('?google-redirection') ? <GoogleLoginSuggestions loginAction={(data) => this.onGSocialLogin('google', data)} key={window.location.href} /> : null}
            <div className="text-start">
                <h1 className=" mb-0 display-2 fw-bold mb-5">Sign up for a free trial</h1>
                <ul className="d-sm-flex d-block f-between register_check text-muted fw-medium">
                    {
                        ["All Premium Features", "No card required", "14-day free trial"].map((txt) => <li className="mb-1" key={txt}><FontAwesomeIcon icon={'check'} iconClass="me-1" />{txt}</li>)
                    }
                </ul>
                <SocialSignIn socialLogin={this.socialLogin} text="Sign up with Google" />
            </div>
            <form onSubmit={this.onFormSubmit} autoComplete="on">
                <div className="mb-8">
                    <Form.Group className="mb-4 mb-xl-5">
                        <Form.Label className="fw-bold " htmlFor="full_name">Full Name</Form.Label>
                        <Form.Control type="text" id="full_name" placeholder="Full Name" size="lg" className={`${afNError ? `is-invalid` : ``}`} value={accountFullName || ''} onChange={this.onFullNameChange} />
                        {afNError ? <span className="invalid-feedback">{accountFullName && accountFullName.length > 0 && containsSpecialChars(String(accountFullName)) ? `Special characters are not allowed. ` : String(accountFullName).length < 3 ? `Full Name length must be at least 3 characters long.` : `Please fill in the field`}</span> : null}
                    </Form.Group>
                    <Form.Group className="mb-4 mb-xl-5">
                        <Form.Label className="fw-bold " htmlFor="email_">Email address</Form.Label>
                        <Form.Control id="email_" size="lg" className={`${aEmailMessageError || aEmailError ? 'is-invalid' : ''} mb-1`} placeholder="you@yourcompany.com" maxLength="255" value={accountEmailId || ''} onChange={this.onEmailChange} />
                        {aEmailEncourager ? <span className="text-muted">Swap personal emails with work emails for a better experience.</span> : null}
                        {aEmailError ? <span className="invalid-feedback">{aEmailMessageError && aEmailMessageError.length > 0 ? aEmailMessageError : `Email is not valid.`}</span> : null}
                    </Form.Group>
                    <Form.Group className="mb-4 mb-xl-5">
                        <Form.Label className="fw-bold " htmlFor="password_">Password</Form.Label>
                        <div className={`position-relative d-block mb-1 ${pwdError ? 'is-invalid mb-1' : ''}`}>
                            <Form.Control type={showPass ? 'text' : 'password'} placeholder="Password" id="password_" size='lg' maxLength="24" onChange={this.checkPassStrength} />
                            <span className="view_pass fs-6 btn btn-icon bg-transparent border-0 text-gray-500 position-absolute translate-middle top-50" onClick={event => this.setState({ showPass: !showPass })}>
                                <FontAwesomeIcon icon={showPass ? 'eye' : 'eye-slash'} />
                            </span>
                        </div>
                        {pwdError ? <span className="invalid-feedback mb-1">Enter valid password.</span> : null}
                        <span className={`${strongPass ? 'text-muted' : 'text-danger'} `}>Password must be at least 8 characters long and include a combination of letters, numbers, and special symbols (e.g., @, #, $, %).</span>
                    </Form.Group>
                    <PwdStrength strength={strength} />
                    <div className="mb-4 mb-xl-5 pt-1  d-none">
                        <Recaptcha
                            ref={ref => this.recaptcha = ref}
                            sitekey={sitekey}
                            onResolved={this.onResolved} />
                        {reCaptcha == false ? <div className="error-message">Please verify that you are not a robot.</div> : null}
                    </div>
                    <Form.Group className="mb-4 mb-xl-5">
                        <Form.Check label="I agree to receive tagshop news and ad tips" type="checkbox" name='acceptAds' id='Ads' inline />
                    </Form.Group>
                </div>
                <div className="d-grid mb-3">
                    <Button type="submit" variant="primary" size="lg" className={`${loginLoader ? ' disabled' : ''}`} onClick={loginLoader ? null : this.onClickToCreateAccount}>
                        <span className="indicator-label" style={{ display: loginLoader ? 'none' : 'block' }}>Sign up with Email</span>
                        <span className="indicator-progress" style={{ display: !loginLoader ? 'none' : 'block' }}> Please wait...
                            <span className="spinner-border spinner-border-sm align-middle ms-2"></span>
                        </span>
                    </Button>
                </div>
            </form>
            <OtherAuthAction text="Already have an account?" link={`${prefix}accounts/login`} linkText={'Login'} />
            <p className="text-muted text-center fs-9 mb-2">By proceeding, you agree to our <Link to={`${WEBSITE_URL}/privacy-policy/`} target="_blank" className="fw-medium" title="Privacy policy">Privacy policy</Link> and <Link to={`${WEBSITE_URL}/terms-of-service/`} target="_blank" className="fw-medium" title="Terms of Service">Terms of Service</Link></p>
            <FormTerms />
        </div>
    }
}



const mapStateToProps = (state) => {
    return {

    }
}
const mapDispatchToProps = dispatch => {
    return {
        userSignUp: (data, history, responseCallback) => dispatch(userSignUp(data, history, responseCallback)),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(SignUp);